export async function loader({
  request
}) {
  const supabase = createSupabaseServerClient(request);
  const [{
    data: assignments
  }, {
    data: courses
  }, {
    data: faculty
  }] = await Promise.all([supabase.from('assignments').select('*, courses (name), users!assignments_faculty_id_fkey (full_name)').order('created_at', {
    ascending: false
  }), supabase.from('courses').select('*').order('name'), supabase.from('users').select('*').eq('role', 'faculty').order('full_name')]);
  return json({
    assignments: assignments || [],
    courses: courses || [],
    faculty: faculty || []
  });
}
export async function action({
  request
}) {
  const supabase = createSupabaseServerClient(request);
  const formData = await request.formData();
  const intent = formData.get('intent');
  if (intent === 'create') {
    const title = formData.get('title');
    const description = formData.get('description');
    const course_id = formData.get('course_id');
    const faculty_id = formData.get('faculty_id');
    const due_date = formData.get('due_date');
    const max_points = parseInt(formData.get('max_points'));
    const {
      data: assignment,
      error
    } = await supabase.from('assignments').insert({
      title,
      description,
      course_id: parseInt(course_id),
      faculty_id: parseInt(faculty_id),
      due_date,
      max_points,
      status: 'active'
    }).select().single();
    if (error) {
      return json({
        error: error.message
      }, {
        status: 400
      });
    }

    // Create announcement for students
    await supabase.from('announcements').insert({
      title: `New Assignment: ${title}`,
      content: `A new assignment "${title}" has been created. Due date: ${new Date(due_date).toLocaleDateString()}`,
      type: 'assignment',
      priority: 'medium',
      target_audience: 'students',
      created_by: parseInt(faculty_id)
    });
    return json({
      success: true,
      assignment
    });
  }
  if (intent === 'delete') {
    const id = formData.get('id');
    const {
      error
    } = await supabase.from('assignments').delete().eq('id', parseInt(id));
    if (error) {
      return json({
        error: error.message
      }, {
        status: 400
      });
    }
    return json({
      success: true
    });
  }
  return json({
    error: 'Invalid action'
  }, {
    status: 400
  });
}
